
<template>
    <div>
     <div @click="refreshCode">验证码</div>
     <div @click="refreshCode">
         <code-check :identifyCode="identifyCode"></code-check>
       </div>
    </div>
</template>


<script>
import CodeCheck from "@/components/login/CodeCheck";

export default {
  name:"code-main",
  data() {
    return {
      identifyCodes: "1234567890abcdefjhijklinopqrsduvwxyz",
      identifyCode: ""
    };
  },
  created() {
    // 初始化验证码
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
  methods: {
    // 重置验证码
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
    },
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    }
  }
};
</script>

<style scope>
body{
    background-color: dimgray;
}
</style>



